import { useFormik } from "formik";
import { useState } from "react";
import { useDispatch } from "react-redux";
import Input from "../../components/Input";
import { editMeal } from "../../redux/mealSlise";
import { useEventListener } from "../../utils/eventListener";
import { prepareValues } from "../../utils/math";
import { MealPopupProps } from "../../utils/types";
import { onKeyPress } from "../../utils/utils";
import { mealValidationSchema } from "../../utils/validation";
import {
  Close,
  Data,
  SavedData,
  EditButton,
  Flex,
  Form,
  Grid,
  MealPopupContainer,
  MealPopupOverlay,
  SaveButton,
  Text,
  Label,
} from "./MealPopup.style";

function MealPopup({ data, isOpen, onClose, onKeydown }: MealPopupProps) {
  const dispatch = useDispatch();

  const [editedForm, setEditedForm] = useState(false);

  useEventListener(isOpen, onKeydown, "keydown");

  const formik = useFormik({
    initialValues: {
      name: data?.name!,
      startWeight: 100,
      protein: Math.round((data?.protein! * 100) / data?.weight!),
      fat: Math.round((data?.fat! * 100) / data?.weight!),
      carb: Math.round((data?.carb! * 100) / data?.weight!),
      weight: data?.weight!,
      day: new Date(data?.timestamp!).getDate(),
      month: new Date(data?.timestamp!).getMonth() + 1,
      year: new Date(data?.timestamp!).getFullYear(),
    },
    enableReinitialize: true,
    validationSchema: mealValidationSchema,
    onSubmit: (values, { resetForm }) => {
      dispatch(
        editMeal({ ...values, id: data!.id, calories, day, month, year })
      );
      onClose();
      setEditedForm(false);
      resetForm();
    },
  });

  const { name, startWeight, protein, fat, carb, weight, day, month, year } =
    formik.values;

  const calories = prepareValues(formik.values);

  return (
    <MealPopupOverlay opened={isOpen} onClick={onClose}>
      <MealPopupContainer opened={isOpen} onClick={(e) => e.stopPropagation()}>
        <Close
          onClick={() => {
            onClose();
            setEditedForm(false);
          }}
        ></Close>
        {editedForm ? (
          <Form onSubmit={formik.handleSubmit}>
            <Input
              formik={formik}
              value={name}
              name="name"
              placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ"
              type={"text"}
            />
            <Flex>
              <Text>–ë–ñ–£ –Ω–∞</Text>
              <Input
                formik={formik}
                value={startWeight}
                name="startWeight"
                placeholder="-–ì—Ä–∞–º–º—ã"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
                startWeight={true}
              />
              <Text>–≥—Ä–∞–º–º</Text>
            </Flex>
            <Grid>
              <Input
                formik={formik}
                value={protein}
                name="protein"
                placeholder="–ë–µ–ª–∫–∏"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
              />
              <Input
                formik={formik}
                value={fat}
                name="fat"
                placeholder="–ñ–∏—Ä—ã"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
              />
              <Input
                formik={formik}
                value={carb}
                name="carb"
                placeholder="–£–≥–ª–µ–≤–æ–¥—ã"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
              />
            </Grid>
            <Input
              formik={formik}
              value={weight}
              name="weight"
              placeholder="–ì—Ä–∞–º–º—ã"
              type={"number"}
              onKeyPress={(evt) => onKeyPress(evt, formik)}
            />
            <Flex>
              <Text>–î–∞—Ç–∞:</Text>
            </Flex>
            <Grid>
              <Input
                formik={formik}
                value={day}
                name="day"
                placeholder="–î–µ–Ω—å"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
              />
              <Input
                formik={formik}
                value={month}
                name="month"
                placeholder="–ú–µ—Å—è—Ü"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
              />
              <Input
                formik={formik}
                value={year}
                name="year"
                placeholder="–ì–æ–¥"
                type={"number"}
                onKeyPress={(evt) => onKeyPress(evt, formik)}
              />
            </Grid>
            <SaveButton
              type="submit"
              aria-label="–°–æ—Ö—Ä–∞–Ω–∏—Ç—å –∏–∑–º–µ–Ω–µ–Ω–∏–µ"
              disabled={!formik.isValid}
            >
              –°–æ—Ö—Ä–∞–Ω–∏—Ç—å –∏–∑–º–µ–Ω–µ–Ω–∏–µ
            </SaveButton>
          </Form>
        ) : (
          <SavedData>
            <Data>{data?.name!}</Data>
            <Flex>
              <Text>–†–∞—Å—Å—á–∏—Ç–∞–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ:</Text>
            </Flex>
            <Data>{data?.calories!} –∫–∫–∞–ª</Data>
            <Grid>
              <Data>
                <Label>–±–µ–ª–∫–∏</Label>
                {data?.protein!} –≥.
              </Data>
              <Data>
                <Label>–∂–∏—Ä—ã</Label>
                {data?.fat!} –≥.
              </Data>
              <Data>
                <Label>—É–≥–ª–µ–≤–æ–¥—ã</Label>
                {data?.carb!} –≥.
              </Data>
            </Grid>
            <EditButton
              type="button"
              aria-label="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å"
              onClick={() => setEditedForm(true)}
            >
              –†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å
            </EditButton>
          </SavedData>
        )}
      </MealPopupContainer>
    </MealPopupOverlay>
  );
}

export default MealPopup;
